/* jqx-element */
.jqx-container {
    box-sizing: border-box;
    font-family: inherit;
    font-size: inherit;
    display: block;
    width: 100%;
    height: 100%;
    outline: none;
    margin: 0;
    padding: 0;
}

    .jqx-container * {
        box-sizing: border-box;
    }

.jqx-popup, /* jqx-list-item popup in jqxListBox during drag&drop */
.jqx-drop-down {
    position: absolute;
    box-sizing: border-box;
    margin: 0;
    z-index: 9999;
    list-style-type: none;
}


/* Glyph icon font */
@font-face {
    font-family: 'jqx-icons';
    src: url('font/jqx.icons.eot');
    src: url('font/jqx.icons.eot#iefix') format('embedded-opentype'), url('font/jqx.icons.woff') format('woff'), url('font/jqx.icons.ttf') format('truetype'), url('font/jqx.icons.svg#fontello') format('svg');
    font-weight: normal;
    font-style: normal;
}

.jqx-arrow {
    width: 100%;
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 1em 1em;
}

.jqx-arrow-up:after,
.jqx-arrow-down:after,
.jqx-arrow-left:after,
.jqx-arrow-right:after,
jqx-date-time-picker .jqx-drop-down-button.jqx-calendar-button:after {
    width: 100%;
    height: 100%;
    font-family: "jqx-icons";
    font-style: normal;
    font-weight: normal;
    color: gray;
    font-size: 7px;
    display: inline-block;
    text-decoration: inherit;
    font-variant: normal;
    text-transform: none;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.jqx-arrow-up:after {
    content: '\e801';
}

.jqx-arrow-down:after {
    content: '\e804';
}

.jqx-arrow-left:after {
    content: '\e803';
}

.jqx-arrow-right:after {
    content: '\e802';
}

.jqx-right-to-left {
    direction: rtl;
}

