/* ripple */
.jqx-ripple {
    display: block;
    position: absolute;
    border-radius: 50%;
    transform: scale(0);
    pointer-events: none;
    animation: ripple 0.35s linear;
    border: none;
    opacity: 0.5;
    background-color: rgba(0,0,0,0.7);
}

    .jqx-ripple.jqx-animate {
        position: absolute;
        animation: ripple 0.65s linear;
    }

@keyframes ripple {
