/* jqx-check-box */

jqx-check-box {
    padding: 1px 2px 1px 2px;
    display: inline-block;
    overflow: visible;
    cursor: pointer;
}

    jqx-check-box[check-mode="input"],
    jqx-check-box[check-mode="label"] {
        cursor: default;
    }

    jqx-check-box:focus {
        outline: none;
    }

        jqx-check-box:focus .jqx-input,
        jqx-check-box:focus .jqx-input {
            border: var(--jqx-border-width) solid #666;
            outline: none;
        }

    jqx-check-box[disabled]:focus .jqx-input,
    jqx-check-box[disabled]:focus .jqx-input {
        outline: none;
    }

    jqx-check-box .jqx-container {
        height: auto;
        position: relative;
        display: flex;
        align-items: center;
    }

    jqx-check-box[right-to-left] .jqx-container {
        justify-content: flex-end;
    }

    jqx-check-box[right-to-left] .jqx-label {
        order: 2;
    }

    jqx-check-box[right-to-left] .jqx-input {
        order: 3;
    }

    jqx-check-box .jqx-label {
        color: var(--jqx-color);
        font-size: inherit;
        font-family: inherit;
        margin: 0px 5px 0px 5px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        width: auto;
    }

    jqx-check-box[check-mode="label"] .jqx-label {
        cursor: pointer;
    }

    jqx-check-box .jqx-input {
        position: relative;
        width: var(--jqx-check-box-default-size);
        min-width: var(--jqx-check-box-default-size);
        height: var(--jqx-check-box-default-size);
        min-height: var(--jqx-check-box-default-size);
        background-color: #fff;
        box-sizing: initial;
        border-style: solid;
        border-width: var(--jqx-border-width);
        border-color: var(--jqx-border-color);
        display: inline-block;
    }

    jqx-check-box[check-mode="input"] .jqx-input {
        cursor: pointer;
    }

    jqx-check-box[readonly],
    jqx-check-box[readonly] .jqx-input,
    jqx-check-box[readonly] .jqx-label {
        cursor: initial;
    }

    jqx-check-box:not([animation='none']) .jqx-input {
        transition: background-color 0.2s ease-out;
    }

    jqx-check-box[checked]:not([animation='none']) .jqx-input {
        transition: background-color 0.2s ease-out;
    }

    jqx-check-box .jqx-input:after {
        content: '';
        width: 100%;
        height: 100%;
        font-family: "jqx-icons";
        font-style: normal;
        font-weight: normal;
        font-size: 8px;
        display: inline-block;
        text-decoration: inherit;
        font-variant: normal;
        text-transform: none;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }

    jqx-check-box[checked]:not([animation='none']) .jqx-input:after {
        animation: check-box-appear 0.2s ease-in;
    }


@keyframes check-box-appear {
    0% {
        transform: scale(0);
        opacity: 0;
    }

    100% {
        transform: scale(1);
        opacity: 1;
    }
}

jqx-check-box[checked] .jqx-input:after {
    content: '\e807';
}

jqx-check-box[checked="null"] .jqx-input:after {
    content: '\e806';
}

jqx-check-box .jqx-animation {
    pointer-events: none;
}

