/* jqx-element */

.jqx-unselectable {
    -webkit-user-select: none;
    -ms-user-select: none;
    -moz-user-select: none;
}

.jqx-hidden {
    display: none;
}

.jqx-visibility-hidden {
    visibility: hidden;
}

.jqx-element-init {
    visibility: hidden;
}

.jqx-element-ready {
    overflow: hidden;
}

.jqx-shown {
    display: block;
}

.jqx-resize-trigger, .jqx-resize-trigger-shrink {
    position: absolute;
    left: 0;
    top: 0;
    transition: 0s;
}

.jqx-resize-trigger-shrink {
    width: 200%;
    height: 200%;
}

.jqx-resize-trigger-container {
    display: block;
    position: relative;
    top: -100%;
    left: 0;
    height: 100%;
    width: 100%;
    min-height: 1px;
    overflow: hidden;
    pointer-events: none;
    z-index: -1;
    visibility: hidden;
}
